import { ChangeDetectionStrategy } from '@angular/compiler';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-securitycenter',
  templateUrl: './securitycenter.page.html',
  styleUrls: ['./securitycenter.page.scss'],
})
export class SecuritycenterPage implements OnInit {
  title: string = 'Security Center';
  mpin: string;
  setmpinFlag: boolean = true;
  currentUser: any;
  isDevice: boolean;
  constructor(private router: Router,
    private cdr: ChangeDetectorRef,
    private dataService: DataService,
    public platform: Platform,
    private apiProvider: ApiService) { }

  ngOnInit() {
    this.mpin = localStorage.getItem("mpin");
    this.currentUser = this.dataService.getCurrentUser();
    this.isDevice = this.platform.is('cordova');

    this.setmpinFlag = this.mpin === "null" ? true : false;
    this.cdr.markForCheck();

  }

  goToHome() {
    this.router.navigate(['/profile']);
  }

  setmpin() {
    this.router.navigate(['/setmpin']);
  }

  changempin() {
    this.router.navigate(['/changempin']);
  }

  changePassword() {
    this.router.navigate(['/changepassword']);
  }
  goToLoginActivity() {
    this.router.navigate(['/loginactivity']);
  }

  setBiometricAuth() {
    let fingerPrint, faceId, customerId;
    customerId = this.currentUser?.customerId;
    faceId = false;
    fingerPrint = false;
    this.registerFaceIdOrFingerPrint(customerId, faceId, fingerPrint);
  }

  registerFaceIdOrFingerPrint(customerId: any, faceId: boolean, fingerPrint: boolean) {
    this.apiProvider.registerFaceIdORFingerPrint(customerId, faceId, fingerPrint).subscribe(res => {
      console.log("Res", res);
    }, err => {
      console.log("Err", err);
    })
  }
}
